<script>
import {
  opthionList,
  getCompanyListCommon,
} from "common/utils/public_function/commonUtils";
export default {
  name: "reportSalesmulrpt",
  data() {
    return {
      authority: {},
      loading: true,
      src: "",
      iframeLoading: true,
      iframeStart: false,
      businessTypeList: {
        options: [],
      }, //业务类型列表
      business_type: null,
      headScreenStart: true,
      tabtton: 0,
      block: false,
      buttonText: ["按月度", "按年度"],
      tabTitle: [
        "按组织统计",
        "按部门统计",
        "按业务员统计",
        "按客户统计",
        "按客户分类统计",
        "按品牌统计",
        "按商品统计",
        "按地区统计",
      ],
      contain: false,
      TableData: {
        data: [],
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "一季度",
            onClick: (picker) => {
              this.tabtton = "";
              this.params.cycle_type = 2;
              var now = new Date();
              var nowYear = now.getYear(); //当前年
              nowYear += nowYear < 2000 ? 1900 : 0; //
              let firstQuarter = nowYear + "-01-01";
              picker.$emit("pick", firstQuarter);
            },
          },
          {
            text: "二季度",
            onClick: (picker) => {
              this.tabtton = "";
              this.params.cycle_type = 2;
              var now = new Date();
              var nowYear = now.getYear(); //当前年
              nowYear += nowYear < 2000 ? 1900 : 0; //
              let firstQuarter = nowYear + "-04-01";
              picker.$emit("pick", firstQuarter);
            },
          },
          {
            text: "三季度",
            onClick: (picker) => {
              this.tabtton = "";
              this.params.cycle_type = 2;
              var now = new Date();
              var nowYear = now.getYear(); //当前年
              nowYear += nowYear < 2000 ? 1900 : 0; //
              let firstQuarter = nowYear + "-07-01";
              picker.$emit("pick", firstQuarter);
            },
          },
          {
            text: "四季度",
            onClick: (picker) => {
              this.tabtton = "";
              this.params.cycle_type = 2;
              var now = new Date();
              var nowYear = now.getYear(); //当前年
              nowYear += nowYear < 2000 ? 1900 : 0; //
              let firstQuarter = nowYear + "-10-01";
              picker.$emit("pick", firstQuarter);
            },
          },
        ],
      },
      business: {
        shortcuts: [
          {
            text: "本周",
            onClick(picker) {
              var now = new Date();
              var nowDayOfWeek = now.getDay();
              const end = new Date();
              const start = new Date();
              start.setTime(
                start.getTime() - 3600 * 1000 * 24 * (nowDayOfWeek - 1)
              );
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "本月",
            onClick(picker) {
              var now = new Date();
              var nowMonth = now.getMonth(); //当前月
              var nowYear = now.getYear(); //当前年
              nowYear += nowYear < 2000 ? 1900 : 0; //
              var monthStartDate = new Date(nowYear, nowMonth, 1);
              var myyear = monthStartDate.getFullYear();
              var mymonth = monthStartDate.getMonth() + 1;
              var myweekday = monthStartDate.getDate();
              if (mymonth < 10) {
                mymonth = "0" + mymonth;
              }
              if (myweekday < 10) {
                myweekday = "0" + myweekday;
              }
              var start = myyear + "-" + mymonth + "-" + myweekday; //本月开始时间
              var monthEndDate = new Date(nowYear, nowMonth + 1, 1);
              var days =
                (monthEndDate - monthStartDate) / (1000 * 60 * 60 * 24);
              var d = new Date(nowYear, nowMonth, days);
              var mymonths = d.getMonth() + 1;
              var myweekdays = d.getDate();
              if (mymonths < 10) {
                mymonths = "0" + mymonths;
              }
              if (myweekdays < 10) {
                myweekdays = "0" + myweekdays;
              }
              var end = myyear + "-" + mymonths + "-" + myweekdays; //本月开始时间
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "上周",
            onClick(picker) {
              var now = new Date();
              var nowDayOfWeek = now.getDay();
              const end = new Date();
              const start = new Date();
              start.setTime(
                start.getTime() - 3600 * 1000 * 24 * (7 + nowDayOfWeek - 1)
              );
              end.setTime(end.getTime() - 3600 * 1000 * 24 * nowDayOfWeek);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "上月",
            onClick(picker) {
              var now = new Date();
              var nowYear = now.getYear(); //当前年
              nowYear += nowYear < 2000 ? 1900 : 0; //
              var nowMonth = now.getMonth(); //当前月
              var stoPMonth = now.getMonth() - 1;
              var monthEndDate = new Date(nowYear, stoPMonth + 1, 1);
              var monthStartDate = new Date(nowYear, stoPMonth, 1);
              var days =
                (monthEndDate - monthStartDate) / (1000 * 60 * 60 * 24);
              var start = nowYear + "-" + nowMonth + "-" + 1;
              var end = nowYear + "-" + nowMonth + "-" + days;
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      tabIndex: 0,
      state: true,
      roleid: "",
      Name: {
        labelName: "销售组织",
        proName: "company_name",
      },
      params: {
        is_cycle: 0,
        cycle_type: 1,
        statistical_type: 0,
        auditor_status: 2,
      },
      timeValue: [
        new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 30),
        new Date(),
      ],
      auditValue: null,
      // period: [],
      companyList: [],
      montsted: {
        period: "month",
        formats: "yyyy-MM-dd",
        place: "请选择月份",
      },
    };
  },
  methods: {
    changebusiness(val) {
      this.params.business_type_ids = val.join(",");
    },
    test() {
      this.iframeLoading = false;
    },
    changeItem(index, item) {
      this.headScreenStart = true;
      this.tabIndex = index;
      this.iframeStart = false;
      this.iframeLoading = true;
      this.params.statistical_type = index;
      switch (index) {
        case 0:
          this.Name = {
            labelName: "销售组织",
            proName: "company_name",
          };
          break;
        case 1:
          this.Name = {
            labelName: "部门",
            proName: "department_name",
          };
          break;
        case 2:
          this.Name = {
            labelName: "业务员",
            proName: "business_name",
          };
          break;
        case 3:
          this.Name = {
            labelName: "客户名称",
            proName: "customer_name",
          };
          break;
        case 4:
          this.Name = {
            labelName: "客户级别",
            proName: "customer_type_name",
          };
          break;
        case 5:
          this.Name = {
            labelName: "品牌名称",
            proName: "brand_name",
          };
          break;
        case 6:
          this.Name = {
            labelName: "商品名称",
            proName: "goods_name",
          };

          break;
        default:
          break;
      }
      if (index == 7) {
        this.roleid = localStorage.getItem("role_id");
        const url = {
          "gyl.taijierp.com":
            "https://report.jybd.cn/webroot/decision/view/report?viewlet=ERP%252F%25E9%2594%2580%25E5%2594%25AE%25E6%25AF%259B%25E5%2588%25A9%25E5%25A4%259A%25E7%25BB%25B4%25E7%25BB%259F%25E8%25AE%25A1%25E8%25A1%25A8-%25E5%258C%25BA%25E5%259F%259F_gyl.cpt&role=",
          "cloud.taijierp.com":
            "https://report.jybd.cn/webroot/decision/view/report?viewlet=ERP%252F%25E9%2594%2580%25E5%2594%25AE%25E6%25AF%259B%25E5%2588%25A9%25E5%25A4%259A%25E7%25BB%25B4%25E7%25BB%259F%25E8%25AE%25A1%25E8%25A1%25A8-%25E5%258C%25BA%25E5%259F%259F_cloud.cpt&role=",
          "scm.taijierp.com":
            "https://report.jybd.cn/webroot/decision/view/report?viewlet=ERP%252F%25E9%2594%2580%25E5%2594%25AE%25E6%25AF%259B%25E5%2588%25A9%25E5%25A4%259A%25E7%25BB%25B4%25E7%25BB%259F%25E8%25AE%25A1%25E8%25A1%25A8-%25E5%258C%25BA%25E5%259F%259F_scm.cpt&role=",
          "xltd.taijierp.com":
            "https://report.jybd.cn/webroot/decision/view/report?viewlet=ERP%252F%25E9%2594%2580%25E5%2594%25AE%25E6%25AF%259B%25E5%2588%25A9%25E5%25A4%259A%25E7%25BB%25B4%25E7%25BB%259F%25E8%25AE%25A1%25E8%25A1%25A8-%25E5%258C%25BA%25E5%259F%259F_xltd.cpt&role=",
          "xinliantongda.taijierp.com":
            "https://report.jybd.cn/webroot/decision/view/report?viewlet=ERP%252F%25E9%2594%2580%25E5%2594%25AE%25E6%25AF%259B%25E5%2588%25A9%25E5%25A4%259A%25E7%25BB%25B4%25E7%25BB%259F%25E8%25AE%25A1%25E8%25A1%25A8-%25E5%258C%25BA%25E5%259F%259F_xltd.cpt&role=",
          "192.168.11.174":
            "https://report.jybd.cn/webroot/decision/view/report?viewlet=ERP%252F%25E9%2594%2580%25E5%2594%25AE%25E6%25AF%259B%25E5%2588%25A9%25E5%25A4%259A%25E7%25BB%25B4%25E7%25BB%259F%25E8%25AE%25A1%25E8%25A1%25A8-%25E5%258C%25BA%25E5%259F%259F_t.cpt&role=",
          "test-www.taijierp.com":
            "https://report.jybd.cn/webroot/decision/view/report?viewlet=ERP%252F%25E9%2594%2580%25E5%2594%25AE%25E6%25AF%259B%25E5%2588%25A9%25E5%25A4%259A%25E7%25BB%25B4%25E7%25BB%259F%25E8%25AE%25A1%25E8%25A1%25A8-%25E5%258C%25BA%25E5%259F%259F_t.cpt&role=",
        };
        this.src = url[location.hostname] + this.roleid;
        this.headScreenStart = false;
        this.contain = false;
        this.iframeStart = true;
      } else {
        this.onSearch("TableDataRef");
      }
      this.params.statistical_type = index;
    },
    change(val) {
      if (val == 1) {
        this.$set(this.params, "timeValue", "");
        this.$set(this.params, "cycle_type", 1);

        this.block = true;
      } else {
        this.$set(this.params, "period", "");
        this.block = false;
      }
    },
    companyChangeId(val) {
      // 组织 change
      this.$set(this.params, "warehouse_id", ""); // 初始化仓库名称
      this.$set(this.params, "allocation_id", ""); // 初始化货位
    },
    warehouseChangeId(val) {
      // 仓库 change
      this.$set(this.params, "allocation_id", ""); // 初始化仓库名称
    },
    buttonCLICK(index) {
      this.tabtton = index;
      if (index == 0) {
        this.monthStar();
      } else {
        this.yearStar();
      }
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL

      if (this.listShow == false) {
        this.$message({
          message: "请查询需要导出的数据",
          type: "error",
        });
        return false;
      }
      Object.assign(this.params, {
        export_type: scheduleType,
      });
      this.$axMethod
        .exportSalesProfitStatisticsUrl(this.params)
        .then((response) => {
          if (response.data.code == 200) {
            window.location.href = response.data.data.url;
          }
        });
    },
    onSearch(addFormInline) {
      this.$refs[addFormInline].validate((valid) => {
        if (valid) {
          this.state = true;
          if (this.params.statistical_type == 0) {
            //选择第一个
            this.$set(this.params, "company_id", "");
            this.$set(this.params, "company_name", "");
          } else {
            if (this.tabIndex !== 0 && !this.params.company_id) {
              this.$message({
                message: "请选择销售组织",
                type: "error",
              });
              this.state = false;
            }
          }

          if (!this.state) {
            this.TableData.data = [];
            return false;
          }
          if (this.auditValue) {
            this.params.auditor_start_time = this.auditValue[0];
            this.params.auditor_end_time = this.auditValue[1];
          } else {
            this.params.auditor_start_time = this.params.auditor_end_time = "";
          }
          if (this.params.is_cycle == 0 && this.params.timeValue.length == 2) {
            //选择第二个
            this.params.start_time = this.params.timeValue[0];
            this.params.end_time = this.params.timeValue[1];
          } else if (this.params.is_cycle == 1) {
            if (!this.params.period) {
              this.$message({
                message: "请选择开始时间",
                type: "error",
              });
              this.state = false;
            }
            delete this.params.end_time;
            this.params.start_time = this.params.period;
            if (!this.state) {
              this.TableData.data = [];
              return false;
            }
          } else {
            delete this.params.end_time;
            delete this.params.start_time;
          }
          this.contain = true;
          this.$axios({
            method: "get",
            url: this.$api.getSalesProfitStatisticsV2,
            params: this.$publicFution.deepCopy(this.params),
          }).then((res) => {
            if (res.data.code == 200) {
              this.TableData = res.data.data;
              this.TableData.data.forEach((item) => {
                item.gross_profit_rate = Number(
                  item.gross_profit_rate.slice(0, -1)
                );
              });
              // if (this.params.timeValue) {
              //   this.params.timeValue = this.params.timeValue.split(",");
              // }
              // 需要注释调改代码块-因放开还会导致代码报错
              // if (this.params.period) {
              //   this.params.period = this.params.period.split();
              // }

              this.loading = false;
            }
          });
        }
      });
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      // this.$set(this, "params", {});
      this.auditValue = [];
      this.business_type = null;
      this.block = false;
      this.$set(this, "params", {
        is_cycle: 0,
        cycle_type: 1,
        statistical_type: 0,
      });
      this.tabIndex = 0;
      this.params.statistical_type = 0;
      this.Name = {
        labelName: "销售组织",
        proName: "company_name",
      };
      this.tabtton = 0;
      this.params.is_cycle = 0;
      this.contain = false;
    },
    getSummaries(param) {
      let _this = this;
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "本页总计";
          return;
        }
        if (index === 5) {
          sums[index] = _this.TableData.desc
            ? _this.TableData.desc.gross_profit_rate
            : "";
          return;
        }
        if (index === 6) {
          sums[index] = _this.TableData.desc
            ? _this.TableData.desc.ring_ratio_growth_rate
            : "";
          return;
        }
        if (index === 7) {
          sums[index] = _this.TableData.desc
            ? _this.TableData.desc.year_on_year_growth_rate
            : "";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 1) {
            sums[index] = _this.TableData.desc.return_goods_num;
          } else if (index === 2) {
            sums[index] = _this.TableData.desc.subtotal_price;
          } else if (index === 3) {
            sums[index] = _this.TableData.desc.cost_price;
          } else if (index === 4) {
            sums[index] = _this.TableData.desc.gross_profit;
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    },
    monthStar() {
      this.authority = JSON.parse(localStorage.getItem("authority"));
      this.$set(this, "montsted", {
        period: "month",
        formats: "yyyy-MM-dd",
        place: "请选择月份",
      });
      this.params.cycle_type = 1;
    },
    yearStar() {
      this.$set(this, "montsted", {
        period: "year",
        formats: "yyyy-MM-dd",
        place: "请选择年",
      });
      this.params.cycle_type = 3;
    },
    getCompany() {
      getCompanyListCommon(this); //获取组织
    },
    formData(date) {
      var d = new Date(date),
        month = "" + (d.getMonth() + 1),
        day = "" + d.getDate(),
        year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    },
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$common.options("businessTypeListOption", this.businessTypeList); //业务类型列表
    this.params.start_time = this.formData(this.timeValue[0]);
    this.params.end_time = this.formData(this.timeValue[1]);
    this.$set(this, "params", {
      is_cycle: 0,
      company_id: Number(localStorage.company_id),
      company_name: localStorage.company_name,
      timeValue: [this.params.start_time, this.params.end_time],
      start_time: this.formData(this.timeValue[0]),
      end_time: this.formData(this.timeValue[1]),
      auditor_status: 2,
    });
    opthionList(this, localStorage);
  },
};
</script>

